/** Media Queries **/
.header {
  display: flex;
  background-color: #24292e;
  padding: 2rem;
  text-align: center;
  align-items: center;
  justify-content: space-between;
  gap: 2rem;
  height: 12rem;
}
@media (min-width: 768px) {
  .header {
    display: grid;
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(4, 1fr);
  }
}

.logo {
  display: flex;
  align-items: center;
  gap: 2rem;
}
.logo a {
  font-family: "Montserrat", sans-serif;
  color: #13b497;
  font-size: 4rem;
}
.logo svg {
  fill: white;
  width: 4.6rem;
  height: 4.6rem;
}

.buscador {
  display: none;
}
@media (min-width: 768px) {
  .buscador {
    display: flex;
    gap: 2rem;
    align-items: center;
  }
  .buscador .formulario {
    display: flex;
  }
  .buscador .formulario input {
    padding: 1rem;
    width: 72.5rem;
    background-color: #1c2126;
    border: none;
    color: #fff;
    border-radius: 1.2rem;
  }
  .buscador .formulario button {
    border: none;
    background-color: #1c2126;
    margin-left: -3rem;
    border-radius: 1.2rem;
    cursor: pointer;
    fill: #fff;
  }
}

.iconos {
  display: flex;
  gap: 2rem;
  justify-content: end;
  grid-column: 3/4;
  grid-row: 1/2;
  grid-auto-flow: column;
}
.iconos .visible {
  display: none;
}
@media (min-width: 768px) {
  .iconos .visible {
    display: block;
  }
}

.icono {
  background-color: #2f363d;
  padding: 1rem;
  border-radius: 1.2rem;
}
.icono svg {
  width: 2.2rem;
  height: 2.15rem;
  fill: #13b497;
}

.perfil {
  display: flex;
  align-items: center;
  gap: 1.5rem;
  border: 1px solid rgba(150, 150, 150, 0.1725490196);
  border-radius: 1.2rem;
  width: 17rem;
}
.perfil .usuario {
  background-color: #c4c4c4;
  width: 5.6rem;
  height: 5.6rem;
  border-radius: 1.2rem;
  display: flex;
  align-items: center;
  justify-content: center;
}
.perfil .usuario svg {
  width: 5.6rem;
  height: 2.6rem;
  fill: #fff;
}
.perfil p {
  display: none;
}
@media (min-width: 768px) {
  .perfil p {
    font-size: 1.6rem;
    display: block;
  }
}
.perfil button {
  background-color: rgba(255, 255, 255, 0);
  border: none;
  cursor: pointer;
  fill: #fff;
}

.titulo {
  display: flex;
  text-align: start;
  padding: 0.7rem;
  align-items: center;
  background-color: #2d3339;
}
.titulo h1 {
  display: none;
}
@media (min-width: 768px) {
  .titulo h1 {
    display: block;
    font-size: 1.6rem;
    font-weight: 600;
    line-height: 2.4rem;
    color: #13b497;
    margin-left: 9.3rem;
  }
}

.contenido-principal {
  margin-top: 4rem;
}
@media (min-width: 768px) {
  .contenido-principal {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: repeat(3, 1fr);
    gap: 3rem;
  }
}

.area-1 {
  grid-column: 1/2;
  grid-row: 1/3;
  padding: 1rem;
  border: 1px solid #272e35;
  border-radius: 1.2rem;
  background-color: #272e35;
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .area-1 {
    margin-bottom: 0;
  }
}
.area-1 .titulo-seccion {
  display: flex;
  justify-content: space-between;
  margin: 2rem 0;
}
.area-1 .titulo-seccion h2 {
  font-size: 2.45rem;
}
.area-1 .info {
  display: flex;
  align-items: center;
  gap: 1rem;
  margin: 2rem 0;
}
.area-1 .info p {
  font-size: 1.47rem;
}
.area-1 .info button {
  background-color: rgba(255, 255, 255, 0);
  border: none;
  cursor: pointer;
  fill: #fff;
}
.area-1 .informacion {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: #20262d;
  padding: 0 2rem;
  border-radius: 1.7rem;
  margin: 5rem 0;
}
.area-1 .informacion h3, .area-1 .informacion p {
  font-size: 1.76rem;
}
.area-1 .seccion-charts {
  display: flex;
  justify-content: space-between;
}
.area-1 .seccion-charts .chart-2 {
  display: none;
}
.area-1 .seccion-charts .chart-1 {
  margin: 0 auto;
}
@media (min-width: 768px) {
  .area-1 .seccion-charts .chart-2 {
    display: block;
  }
}

.area-2 {
  grid-column: 2/3;
  grid-row: 1/2;
  border-radius: 1.2rem;
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .area-2 {
    margin-bottom: 0;
  }
  .area-2 img {
    min-height: 35rem;
  }
}
.area-2 img {
  border-radius: 1.2rem;
}

.area-3 {
  grid-column: 1/2;
  grid-row: 3/4;
  gap: 2rem;
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .area-3 {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(2, 1fr);
    margin-bottom: 0;
  }
}
.area-3 .chart-4,
.area-3 .chart-5,
.area-3 .chart-6,
.area-3 .chart-7 {
  margin-bottom: 2rem;
  background-color: #272e35;
  border-radius: 1.2rem;
}
@media (min-width: 768px) {
  .area-3 .chart-4,
  .area-3 .chart-5,
  .area-3 .chart-6,
  .area-3 .chart-7 {
    margin-bottom: 0;
  }
}
.area-3 .chart-7 {
  display: none;
}
@media (min-width: 768px) {
  .area-3 .chart-7 {
    display: block;
    margin: 0;
    padding: 0;
  }
  .area-3 .chart-7 p {
    margin: 0;
    padding: 0;
  }
}

.area-4 {
  border-radius: 1.2rem;
  grid-column: 2/3;
  grid-row: 2/4;
}
.area-4 .titulo-galeria {
  text-align: center;
}
@media (min-width: 768px) {
  .area-4 .titulo-galeria {
    text-align: start;
  }
}
.area-4 .swiper {
  border-radius: 1.2rem;
  height: 300px;
}
@media (min-width: 768px) {
  .area-4 .swiper {
    width: 60rem;
    min-height: 68.1rem;
  }
}
.area-4 .swiper .swiper-button-prev,
.area-4 .swiper .swiper-button-next {
  display: flex;
  color: #20262d;
  align-items: baseline;
}

