@use 'base/variables' as v;
@use 'base/mixins' as m;


.tabs{

    @include m.tablet{

        display: flex;
        
    }

    button{

        background-color: v.$azul;
        color: v.$blanco;
        border: none;
        padding: 1rem;
        display: block;
        width: 100%;
        text-transform: uppercase;
        margin-bottom: 1rem;
        font-weight: bold;
        transition: 300ms ease;

        &:hover{

            cursor: pointer;
            background-color: darken(v.$azul, 10%);

        }

        &.actual{

            background-color: v.$blanco;
            color: v.$azul;
        }
    }
}